.navbar .correct{
    -webkit-animation:.4s correct;
    animation:.4s correct
}

.navbar .incorrect{
    -webkit-animation:.4s incorrect;
    animation:.4s incorrect
}

.navbar{
    background:#5133a4;
    min-height:70px;
    position:relative;
    width:100%;
    -webkit-box-shadow:0 14px 28px rgba(0,0,0,.25),0 10px 10px rgba(0,0,0,.22);box-shadow:0 14px 28px rgba(0,0,0,.25),0 10px 10px rgba(0,0,0,.22);
    color:#fff;
    font-size:calc(10px + 14 * (100vw - 320px)/ 800);
    z-index:99;
    white-space:nowrap
}
.navbar a{
    text-decoration:none;
    color:#fff;font-weight:900
}
